<template>
    <div class="certification">
        <p class="title">賣家實名認證</p>
        <p class="sub-title">CERTIFICATION</p>
        <div class="certification-input">
            <p class="note">※ 請如審慎填寫真實資料，如有偽造變造或冒用等情形，本公司將保留相關的司法追訴權，且依內部規定將相關帳號進行停權或解除契約之關係。</p>
            <ul>
                <li>
                    <p class="input-title">真實性名</p>
                    <el-input v-model="username" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">身分字號</p>
                    <el-input v-model="number" placeholder="請輸入"></el-input>
                </li>
                <li style=" width: 100%;" class="certification-date">
                    <p class="input-title">發證日期</p>
                    <div class="input-date">
                        <el-date-picker 
                            v-model="date"
                            type="date"
                            placeholder="选择日期">
                        </el-date-picker>
                    </div>
                </li>
                <li style=" width: 100%;">
                    <p class="input-title">上傳證件照</p>
                    <div class="upload-input-img">
                        <el-input v-model="img" placeholder="請輸入"></el-input>
                        <span>選擇圖片</span>
                    </div>
                    
                </li>
                <li style=" width: 100%;" class="img-box" v-if="img">
                    <img :src="require('@/assets/img/member/'+ img)" alt="" srcset="">
                </li>
                
            </ul>
            
        </div>
        <div class="car-btn">確認送出</div>
    </div>
</template>

<script>
export default {
    name:'certification',
    components: {
    },
    data() {
        return {
            username:'王曉明',
            number:'A123456789',
            date: '',
            img: 'ROC_mibunsho.png',
        }
    }


}
</script>

<style lang='scss' scoped>
.certification {
    padding: 100px 0;
    text-align: center;
    max-width: 850px;
    width: 80%;
    margin: 0 auto;
    .title {
        color: #333333;
        font-size: 40px;
        font-weight: bold;
    }
    .sub-title {
        color: #ef6d38;
        font-size: 25px;
    }
    .certification-input {
        margin-top: 50px;
        text-align: left;
        .note {
            color: red;
            line-height: 30px;
            padding: 0 10px;
        }
        .input-title {
            color: #ef6d38;
            padding-left: 25px;
            margin-bottom: 10px;
            text-align: left;
        }
        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            li {
                margin-top: 30px;
                padding:0 10px;
            }
        }
        .upload-input-img {
                position: relative;
                .upload-input { 
                    width: 100%;
                }
                span {
                    right: 0px;
                    position: absolute;
                    background: #ef6d38;
                    color: #fff;
                    border-radius: 0px 30px 30px 0px;
                    line-height: 60px;
                    padding: 0 10px; 
                    cursor: pointer;
                    &::after {
                        content: " ";
                        position: absolute;
                        left: -30px;
                        border-style: solid;
                        border-width: 0 0 60px 30px;
                        border-color: transparent transparent #ef6d38 transparent;
                    }  
                }
        }
        .img-box {
            background: #f5f5f5;
            padding: 20px;
            text-align: center;
            img {
                width: 100%;
            }
        }
        
    }
    @media screen and (max-width: 1330px) {
        .upload-input-img ,.input-title,.input-date{
            max-width: 400px;
            margin: auto;
        }

    }
    @media screen and (max-width: 768px) {
        .upload-input-img,.input-title,.input-date {
            width: 300px;
        }

    }
}

</style>


